.sidebar-close-icon {
    opacity: 0;
    display: none;
}

.sidebar-menu {
    position: absolute;
    opacity: 0;
    z-index: 30;
    top: 0;
    left: 0;
    display:-webkit-flex;
    display:flex;
    width: 100vw;
    height: 100vh;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    color: #0046BB;
    background: #C7EAF1;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 30px;
}

/*Tablet Size for hiding navlinks*/
@media (max-width: 865px) {
    .sidebar-close-icon {
        display: block;
        opacity: 1;
        -webkit-transform: translateY(2px);
                transform: translateY(2px);
        font-size: 32px;
    }
}

/*Mobile Landscape Size*/
@media (max-width: 768px) {
    .sidebar-close-icon {
        display: block;
        font-size: 32px;
        opacity: 100%;
        right: 26.5px;
        top: 4px;
        margin-top: 0rem;
        position: absolute;
        color: #0046BB;
        z-index: 40;
    }

    .sidebar-menu {
        position: absolute;
        opacity: 0;
        z-index: 35;
        top: 0;
        left: 0;
