/*Mobile Landscape Size*/
@media (max-width: 768px) {
    .review-section-wrapper-titles--primary {
        font-size: 28px;
    }
}

@media (max-width: 660px) {
    .review-section {
        padding: 0 50px;
    }

    .review-section-wrapper {
        min-width: 400px;
    }
    .next {
        position: absolute;
        top: 85px;
        right: 10vw;
    }
    
    .previous {
        position: absolute;
        top: 85px;
        left: 8vw;
    }

    .review-image {
        max-width: 100%;
        max-height: 100%;
    }

    .reviewer-name {
        margin: 12px 0 0 0;
        font-size: 20px;
    }

    .review-comment {
        margin: 12px 0;
        font-size: 18px;
        max-width: 400px;
        text-align: center;
    }
}

/*Mobile Portrait Size*/
@media (max-width: 480px) {
    .review-section-wrapper {
        min-width: auto;
    }

    .review-section-wrapper-titles--primary {
        margin-top: 60px;
        text-align: center;
        font-size: 24px;
    }

    .review-section-wrapper-titles--secondary {
        font-size: 18px;
    }

    .review-image {
        max-width: 150px;
        max-height: 150px;
    }

    .next {
        top: 60px;
        right: 1px
    }

    .previous {
        top: 62px;
        left: -1vw
    }

    .reviewer-name {
        font-size: 16px;
    }

    .review-comment {
        font-size: 14px;
    }
}
.footer {
    background: #33495C;
    padding: 0 100px;
    font-family: 'IBM Plex Sans', sans-serif;
    color: #fff;
}

.footer-wrapper {
    display: grid;
    height: auto;
    margin: 20px auto;
    margin-left: 60px;
    padding: 90px 0;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(2, auto);
}

.footer-wrapper h4 {
     font-size: 28px;
     margin: 0 0 16px 0;
 }

 .footer-column {
     display: -webkit-flex;
     display: flex;
     -webkit-flex-flow: column wrap;
             flex-flow: column wrap;
     -webkit-justify-content: stretch;
             justify-content: stretch;
     -webkit-align-items: flex-start;
             align-items: flex-start;
 }

 .footer-column p {
     margin: 12px 0;
 }

 .footer-copyright-info {
     font-size: 14px;
     margin: 100px auto;
     margin-bottom: 20px;
     grid-column-start: 1;
     grid-column-end: span3;
 }

 .social-icon img {
    margin: 8px 0;
    width: 40px;
    height: 40px;
}

 /*Tablet Size*/
@media (max-width: 992px) {
    .footer {
        padding: 0 50px;
    }
    .footer-wrapper {
        grid-gap: 20px;
        margin-left: 40px;
    }

}

/*Mobile Landscape Size*/
@media (max-width: 768px) {
    .footer-wrapper {
        margin-left: 0px;
    }
}

@media (max-width: 660px) {
    .footer-wrapper {
        display: grid;
        margin: 0px auto;
        margin-left: 0px;
        padding: 100px 0 20px 16px;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto auto auto;
    }

    .footer-wrapper h4 {
        font-size: 24px;
        margin: 0 0 12px 0;
    }

    /* .footer-column {
        align-items: center;
    } */
    .footer-column p {
        margin: 8px 0;
        font-size: 14px;
    }

    .footer-column:nth-child(3) {
        grid-column-start: 1;
        grid-row-start: 2 ;
    }

    .footer-column:nth-child(4) {
        grid-column-start: 2;
        grid-row-start: 2 ;
    }

    .footer-copyright-info {
        margin: 30px auto;
        font-size: 12px;
    }
}

/*Mobile Portrait Size*/
@media (max-width: 480px) {
    .footer {
        padding: 10px;
    }

    .footer-wrapper h4 {
        font-size: 18px;
    }

    .footer-column p {
        font-size: 12px;
    }

    .footer-copyright-info {
        margin-top: 20px;
        font-size: 10px;
    }
    
    .social-icon img {
        margin: 8px 0;
        width: 25px;
        height: 25px;
    }
}
